@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Advent+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300;400&display=swap);
body {
  background-color: rgb(234, 237, 237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
  background: rgb(234, 237, 237); /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
  background-color: #c0392b; /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 3px solid rgb(234, 237, 237); /*  creates padding around scroll thumb */
}

* {
  margin: 0;
}

.header {
  background-color: #1c2833;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 0.5rem 10rem 0.5rem 12rem;
}

.header__logoLink {
  text-decoration: none;
}

.header__logo {
  /* color: antiquewhite; */
  color: #c0392b;
  font-family: "Advent Pro", sans-serif;
  font-size: 2.5rem;
  margin-left: 1.5rem;
  margin-right: 1rem;
  padding-top: 3px;
  padding-bottom: 5px;
  object-fit: contain;
}

nav > div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header__nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-right: 3rem;
}

.header__link {
  color: beige;
  text-decoration: none;
  margin: 0.5rem 1rem;
}

.header__cart {
  position: relative;
}

.header__cart span {
  padding-left: 0.2rem;
  padding-right: 0.2rem;
  position: absolute;
  bottom: 55%;
  left: 80%;
  background-color: #c0392b;
  border-radius: 1rem;
}

.header__option {
  font-size: 1.5rem;
}

.home {
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
  /* background-color: #ecebea; */
}

.home__image {
  width: 100%;
  height: 600px;
  -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
          mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  z-index: -1;
  margin-bottom: -10rem;
  position: relative;
}

.home__row {
  display: -webkit-flex;
  display: flex;
  z-index: 1;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.product {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 400px;
  padding: 0.8rem;
  /* border: 1px solid black; */
  margin: 1rem;
  /* border-radius: 1rem; */
  background-color: #ffffff;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.product > img {
  max-height: 300px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 1rem;
  /* border-radius: 1rem; */
}

.product__info {
  font-family: "Noto Sans TC", sans-serif;
  line-height: 1.2rem;
}

.product > button {
  background-color: #1c2833;
  color: #c0392b;
  padding: 0.5rem;
  border-radius: 0.5rem;
  font-family: "Noto Sans TC", sans-serif;
  margin-top: 0.8rem;
  font-size: 1.2rem;
  outline: none;
  border: none;
}

.login {
  margin-top: 15rem;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.login__container {
  width: 300px;
  background-color: white;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 20px;
}

.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}

.login__container > form > h5 {
  margin-bottom: 5px;
}

.login__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
}

.login__container > p {
  margin-top: 15px;
  font-size: 12px;
}

.login__signInButton {
  color: #c0392b;
  padding: 0.4rem;
  width: 100%;
  height: 30px;
  background-color: #1c2833;
  font-size: 1rem;
  border: none;
  border-radius: 0.2rem;
  margin-top: 0.3rem;
  margin-bottom: 0.2rem;
}

.login__registerButton {
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
  font-size: 0.9rem;
}

.checkoutProduct {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 70%;
  padding: 2rem;
  background-color: white;
  margin-bottom: 1rem;
  margin-right: 1rem;
  margin-left: 2rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.checkoutProduct > img {
  max-height: 300px;
  object-fit: contain;
  margin-bottom: 0.5rem;
}

.checkoutProduct__button {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.checkoutProduct__button--buy {
  color: #c0392b;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-family: "Noto Sans TC", sans-serif;
  /* margin-top: 1rem; */
  background-color: #1c2833;
  /* margin-bottom: 0.5rem; */
  font-size: 1.2rem;
  outline: none;
  border: none;
  width: 100%;
}

.checkoutProduct__button--buyHalf {
  color: wheat;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-family: "Noto Sans TC", sans-serif;
  margin-top: 1rem;
  background-color: green;
  /* margin-bottom: 0.5rem; */
  font-size: 1.2rem;
  outline: none;
  border: none;
  width: 100%;
}

.checkoutProduct__button--rent {
  color: black;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-family: "Noto Sans TC", sans-serif;
  margin-top: 1rem;
  background-color: #d4ac0d;
  /* margin-bottom: 0.5rem; */
  font-size: 1.2rem;
  outline: none;
  border: none;
  width: 100%;
}

.checkoutProduct__button--remove {
  color: #1c2833;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-family: "Noto Sans TC", sans-serif;
  margin-top: 1rem;
  /* margin-bottom: 0.4rem; */
  background-color: #c0392b;
  font-size: 1.2rem;
  outline: none;
  border: none;
}

.checkout {
  max-width: 1500px;
  margin: auto;
}

.checkout__title {
  text-align: center;
  padding-bottom: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid lightgray;
}

.checkout__product {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.checkout__left {
  width: 100%;
}

.checkout__right {
  position: fixed;
  right: 13%;
  top: 10rem;
}

.subtotal {
  padding: 2rem;
  background-color: white;
  border: 1px solid #1c2833;
}

.subtotal__btn {
  margin-top: 1rem;
  color: #1c2833;
  border: 1px solid #1c2833;
  padding: 0.5rem;
  border-radius: 0.2rem;
  font-size: 0.8rem;
  font-weight: bold;
}

.productDetail__main {
  max-width: 1500px;
  margin: auto;
}

.productdetail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  /* flex-wrap: wrap; */
}

.product__img img {
  width: 600px;
  height: auto;
  object-fit: contain;
  margin: 1rem;
}

.productDetail__info {
  max-width: 45%;
  margin: 1rem;
}

.productdetail button {
  color: #c0392b;
  padding: 0.4rem;
  border-radius: 0.5rem;
  font-family: "Noto Sans TC", sans-serif;
  margin-top: 0.8rem;
  background-color: #1c2833;
  margin-bottom: 0.2rem;
  font-size: 1.2rem;
  outline: none;
  border: none;
  width: 50%;
}

.product__policy {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  /* background-color: beige; */
  -webkit-justify-content: center;
          justify-content: center;
}

.product__policy--rent {
  max-width: 45%;
  margin: 1rem;
  padding: 1rem;
  background-color: #ffffff;
  color: #1c2833;
}

.product__policy--rent h1 {
  padding: 1rem;
  text-align: center;
  color: #c0392b;
}

.product__policy--buy {
  max-width: 45%;
  background-color: #ffffff;
  margin: 1rem;
  color: #1c2833;
  padding: 1rem;
}

.product__policy--buy h1 {
  padding: 1rem;
  text-align: center;
  color: #c0392b;
}

